.main-wrapper {
  height: 100vh;
  width: 100%;
  display: flex;
}

#nav {
  display: flex;
  flex-direction: column;
  width: 30%;
  min-width: 100px;
  padding: 1rem;
  font-size: 16px;
  overflow: auto;
  border-right: 1px solid #AAAAAA;
}

#container {
  height: 100%;
  flex-grow: 1;
  position: relative;
  display: flex;
  overflow: hidden;
}

#attach {
  flex-grow: 1;
  height: 100%;
}

#nav .file {
  cursor: pointer;
  word-break: break-all;
}

.spinner {
  align-self: center;
  justify-self: center;
}